import React, { Component } from 'react'
import 'react-photo-view/dist/index.css';
import Zmage from 'react-zmage'
export default class GalleryItem extends Component {
    render() {
        const { imgSrc, tag, intro1, intro2 } = this.props
        return (
            <div className="galleryImg w-inline-block">
                <div className="galleryHoverShadow" onClick={() => Zmage.browsing({ backdrop:"white",src:imgSrc })}>
                    <div className="galleryImgTag">
                        <p className="galleryImgTagText">{tag}</p>
                    </div>
                    <p className="galleryimgintro">{intro1}<br />{intro2}</p>
                </div>
                <img src={imgSrc} loading="lazy" sizes="(max-width: 1596px) 85vw, 1357px" alt="loading..." />
            </div>
        )
    }
}