import React, { Component, useEffect, useState } from 'react'
import { Route } from 'react-router-dom'
import { useLocation } from 'react-router'

import Poptrack from './Poptrack'
import Xinhua from './Xinhua'
import ThreeMoney from './ThreeMoney'
import GlobalZJU from './GlobalZJU'
import ThunderAI from './ThunderAI'

import Footer from '../../components/Footer'
import BackToTop from '../../components/BackToTop'
import NavSection from '../../components/Nav'

const WorkPage=()=>{

  var location = useLocation()
  console.log(location.pathname);
  var colorArr= location.pathname.split("/");

  const [color,setColor]=useState('black');

  useEffect(()=>{
    window.scrollTo(0, 0)
    switch(colorArr[2]) {
      case 'poptrack':
        setColor("blue")
        break;
      case 'xinhua':
        setColor("blue")
        break;
      case 'threeMoney':
        setColor("blue")
        break;
      case 'globalZJU':
        setColor('#084782')
        break;
      case 'thunderAI':
        setColor("blue")
        break;
      } 
  },[])


  return (
    <div className="workpage" style={{border:`16px solid ${color}`}}>
      <NavSection />
      <Route path="/projects/poptrack" component={Poptrack} />
      <Route path="/projects/xinhua" component={Xinhua} />
      <Route path="/projects/threeMoney" component={ThreeMoney} />
      <Route path="/projects/globalZJU" component={GlobalZJU} />
      <Route path="/projects/thunderAI" component={ThunderAI} />
      <Footer />
      <BackToTop />
    </div>
  )
};
export default WorkPage;