import React, { useState } from 'react'
import { NavLink, Route ,Switch} from 'react-router-dom'

import Header from '../../components/Header'
import Footer from '../../components/Footer'
import Nav from '../../components/Nav'
import WorkSection from '../WorkSection'
import BlogSection from '../BlogSection'
import GallerySection from '../GallerySection'
import AboutSection from '../AboutSection'
import './index.css'
import 'animate.css';
const HomePage=()=>{
  const [color,setColor]=useState('black')
  function changeColor(value){
    setColor(value)
  }
    return (
      <div className="main_border" style={{border:`16px solid ${color}`}}>
        <div className="main_border_updown" style={{background:color,top:0}}></div>
        <div className="main_border_updown" style={{background:color,bottom:0}}></div>
        <Header />
        <Nav/>
        <main id="MainPart" className="mainPart">
            <div data-duration-in="300" data-duration-out="100" className="tabs w-tabs">
              <div className="tabs-menu w-tab-menu" role="tablist">
                  <NavLink activeClassName="w--current" className="tabLink w-inline-block w-tab-link" to={`/home/work`}onClick={()=>changeColor('black')}>WORKS</NavLink>
                  <NavLink activeClassName="w--current" className="tabLink w-inline-block w-tab-link" to={`/home/blog`}onClick={()=>changeColor('black')}>BLOG</NavLink>
                  <NavLink activeClassName="w--current" className="tabLink w-inline-block w-tab-link" to={`/home/gallery`} onClick={()=>changeColor('black')}>GALLERY</NavLink>
                  <NavLink activeClassName="w--current" className="tabLink w-inline-block w-tab-link" to={`/home/about`} onClick={()=>changeColor('rgba(103, 255, 50)')}>ABOUT</NavLink>
              </div>
              <div className="tabs-content w-tab-content">
                <div className="w-tab-pane w--tab-active">
                  <Switch>
                    <Route path="/home/work" component={WorkSection} />
                    <Route path="/home/blog" component={BlogSection} />
                    <Route path="/home/gallery" component={GallerySection} />
                    <Route path="/home/about" component={AboutSection} />
                    </Switch>
                  </div>
              </div>
            </div>
        </main>
        <Footer />
      </div>

    )
  };

  export default HomePage;
